.forecast {
  list-style: none;
  text-transform: uppercase
}
.forecast:after {
  content: '';
  display: block;
  clear: both;
}
.forecast > li {
  float: left;
  width: 14.28571%;
  text-align: center;
  border-right: 1px solid white;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: pointer
}
.forecast > li:last-child {
  border-right: unset;
}
.forecast .icon {
  width: 100%;
}



